<template>
  <div class="LoginView"
    :style="{'background-image': background}">
    <div class="flexbox">
      <SigninInput 
        v-if="page === 0" 
        @toggle="togglePage"
        />
      <SignupInput 
        v-else-if="page === 1" 
        @toggle="togglePage"
        />
    </div>
  </div>
</template>

<script>
import SigninInput from './components/SigninInput.vue';
import SignupInput from './components/SignupInput.vue';

export default {
  name: 'LoginView',
  props: {},
  data: ()=>{
    return {
      page : 0,
      list : ["signIn", "signUp"],
      background : `url(${global.CONFIG.assetURL}/background/8.jpg)`,
    }
  },
  components:{
    SigninInput,
    SignupInput
  },
  methods:{
    togglePage:function(){
      this.page = (this.page+1)%2;
    }
  },
}
</script>

<style scoped>
.LoginView{
  width: 100%;
  height: 100%;
}
.flexbox{
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>