<template>
  <div class="SigninInput">
    <div class="flexbox">
      <InputForm @submit="onSubmit"/>
      <button @click="onToggle">토글</button>
    </div>
  </div>
</template>

<script>
import InputForm from '../../../components/input_ID_PW.vue'

export default {
  name: 'SigninInput',
  props: ['width','height'],
  data:()=>{
    return {
      val:"",
    }
  },
  methods: {
    onSubmit :function(e){
      this.val = e;
    },
    onToggle :function(){
      this.$emit("toggle", 1);
    }
  },
  components:{
    InputForm
  }
}
</script>

<style scoped>
.SigninInput{
  height: 550px;
  width: 550px;
  background-color: white;
  border-radius: 10%;
  border: 5px solid orange;
}
.flexbox{
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
</style>
