<template>
  <div class="Input">
    <div class="flexbox">
      <input v-model="id" placeholder="ID">
      <input v-model="pw" placeholder="Password">
      <button @click="submit">제출</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data:()=>{
    return {
      id : "",
      pw : "",
    };
  },
  methods:{
    submit:function(){
      this.$emit("submit",this.value);
    }
  },
}
</script>

<style scoped>
.flexbox{
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
</style>
